.switch {
  position: relative;
  display: inline-block;
  width: 36px;
  height: 16px; }
  .switch input {
    opacity: 0;
    width: 0;
    height: 0; }

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--pmbet-cream);
  -webkit-transition: var(--pmbet-transition);
  transition: var(--pmbet-transition); }
  .slider::before {
    position: absolute;
    content: "";
    height: 16px;
    width: 16px;
    left: 2px;
    bottom: 0;
    background-color: var(--pmbet-gray-lighter--2);
    -webkit-transition: var(--pmbet-transition);
    transition: var(--pmbet-transition); }

input:checked + .slider {
  background-color: var(--pmbet-cream); }

input:focus + .slider {
  box-shadow: 0 0 1px var(--pmbet-gray-lighter--2); }

input:checked + .slider:before {
  -webkit-transform: translateX(16px);
  -ms-transform: translateX(16px);
  transform: translateX(16px);
  background-color: var(--pmbet-green); }

/* Rounded sliders */
.slider.round {
  border-radius: 18px; }

.slider.round:before {
  border-radius: 50%; }

